<template>
  <div class="course-container" id="course-detail">
    <el-row>
      <el-col>
        <el-card class="card-box" shadow="never">
          <!--课程出售和学习情况概述-->
          <el-row>
            <el-col>
              <el-card class="card-box bordered" shadow="hover">
                <div>课程已售情况 ：  已售12983份 共售卖23874元  北京  售263份占比20% 共计32984元  昆明  售263份占比50%  共计32984元</div>
                <div>课程学习情况 ：  23%已完成    %50 学习中   27%未开始学习</div>
              </el-card>
            </el-col>
          </el-row>
          <!--课程内容概述-->
          <el-row>
            <el-col :span="10">
              <div class="poster-detail">
                <el-image style="width: 100%;height: 250px;"
                  src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png" fit="cover"></el-image>
              </div>
              <div>
                <center>
                  <el-button type="primary" style="width: 40%;">
                    <i class="fa fa-video-camera"></i>&nbsp;&nbsp;&nbsp;立即观看</el-button>
                </center>
              </div>
            </el-col>
            <el-col class="ptr-10" :span="13" :offset="1">
              <el-form v-model="course" label-width="100px">
                <el-form-item label="课程名称">
                  <el-input v-model="course.courseName" placeholder="课程名称"></el-input>
                </el-form-item>
                <el-form-item label="作者名称">
                  <el-input v-model="course.author" placeholder="作者名称"></el-input>
                </el-form-item>
                <el-form-item label="课程描述">
                  <el-input v-model="course.description" placeholder="课程描述"></el-input>
                </el-form-item>
                <el-form-item label="课程类型">
                  <el-input v-model="course.courseType" placeholder="课程类型"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
          </el-row><br>
          <!--课程详情-->
          <el-row>
            <el-col>
              <div><h3>课程详情</h3></div>
              <el-form v-model="course">
                <el-form-item label="课程介绍">
                  <el-input type="textarea" rows="4" v-model="course.courseName" placeholder="课程介绍"></el-input>
                </el-form-item>
                <el-form-item label="练习功效">
                  <el-input type="textarea" rows="4" v-model="course.author" placeholder="练习功效"></el-input>
                </el-form-item>
                <el-form-item label="适用人群">
                  <el-input type="textarea" rows="4" v-model="course.description" placeholder="适用人群"></el-input>
                </el-form-item>
                <el-form-item label="课程准备">
                  <el-input type="textarea" rows="4" v-model="course.courseType" placeholder="课程准备"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
/**
* @author 吴永军
* @description 课程详情
*/
import { fakeCourseDetail, transCourseDetail } from './table.config'
export default {
  name: 'courseDetail',
  data () {
    return {
      course: {}
    }
  },
  created () {
    this.course = transCourseDetail(fakeCourseDetail)
  }
}
</script>
<style>
@import "../../../assets/styles/courses.css";
</style>



